*{
    box-sizing: border-box;
}

.principal{
    display: flex;
}

.moi{
    display: flex;
    justify-content: center;
    color: white;
    width: 50%;
    flex-direction: column;
    margin-right: 20px;
    margin-left: 20px;
    margin-top: 12rem;
    
}

.titre{
    width: 100%;
    text-align: center;
    margin-bottom: 2rem;

}

.texte{
    width: 100%;
    text-align: center;
}

.texte p{
    font-size: 100%;
}


.image {
    width: 50%;
   display: flex;
   justify-content: center;
}

.image img{
    width: 50%;
    margin-top: 10%;
    margin-bottom: 10%;
}

.boutton{
    margin-top: 5rem;
    display:flex ;
    width: 100%;
    justify-content: center;
}

.boutton a{
    position: relative;
    padding: 25px 30px;
    margin: 40px 0; color: #03e9f4;
    text-decoration: none;
    text-transform: uppercase;
    transition: 0.5s; letter-spacing: 4px;
    overflow: hidden; margin-right: 50px;
}

.boutton a:hover{
    background: #03e9f4; color: #050801;
}

a span{
    position: absolute;
    display: block;
}

a span:nth-child(1){
    top: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background: linear-gradient(90deg,transparent,#03e9f4);
    animation: animationUne 1s linear infinite;
}

@keyframes animationUne{
    0%{
        left: -100%;
    }
    50%,100%{
        left: 100%;
    }
}

a span:nth-child(2){
    top: -100%;
    right: 0;
    width: 2px;
    height: 100%;
    background: linear-gradient(180deg,transparent,#03e9f4);
    animation: animationDeux 1s linear infinite;
    animation-delay:0.25s ;
}

@keyframes animationDeux{
    0%{
        top: -100%;
    }
    50%,100%{
        top: 100%;
    }
}

a span:nth-child(3){
    bottom: 0;
    right: 0;
    width: 100%;
    height: 2px;
    background: linear-gradient(270deg,transparent,#03e9f4);
    animation: animationTrois 1s linear infinite;
    animation-delay:0.50s ;
}

@keyframes animationTrois{
    0%{
        right: -100%;
    }
    50%,100%{
        right: 100%;
    }
}

a span:nth-child(4){
    bottom: 100%;
    left: 0;
    width: 2px;
    height: 100%;
    background: linear-gradient(360deg,transparent,#03e9f4);
    animation: animationQuatre 1s linear infinite;
    animation-delay:0.75s ;
}

@keyframes animationQuatre{
    0%{
        bottom: -100%;
    }
    50%,100%{
        bottom: 100%;
    }
}


@media (max-width: 700px){
    .principal{
        display: flex;
        flex-direction: column-reverse; 
        justify-content: center;
    }

    .image {
        color: white; 
       display: flex;
       justify-content: center;
       width: 100%;
    }
    
    .image img{
       width: 100%;
       margin-left: 3rem;
       margin-right: 3rem;
    }
    
    .moi{
        display: flex;
        justify-content: center;
        color: white;
        width: 100%;
        
    }
    
    .moi h2{
        margin-top: 0.8rem;
        margin-left: 15%;
        color: red;
    }
    
}

@media (max-width: 420px){
    .image img{
        width: 100%;
       padding: 20px;
       margin-top: 2px;
     }
}
